.header {
  color: #fff;
  background-color: #007bff;
  justify-content: space-around;
  align-items: center;
  gap: 3rem;
  height: 3rem;
  display: flex;

  & div {
    font-weight: bold;
  }
}

.wrapper {
  gap: 1rem;
  margin-top: .75rem;
  display: flex;

  & .product {
    flex-wrap: wrap;
    justify-content: center;
    gap: .75rem;
    width: 70%;
    padding: 1rem;
    display: flex;
  }

  & .cart {
    flex-flow: column wrap;
    gap: 1rem;
    width: 30%;
    padding: 1rem;
    display: flex;
  }
}

.shimmer {
  background-color: #d3d3d3;
  width: 10rem;
  height: 15rem;
}

.shimmerWrapper {
  flex-wrap: wrap;
  gap: .75rem;
  padding: .75rem;
  display: flex;
}

.product {
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 1rem;
  padding: 1rem;
  display: flex;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  justify-content: space-between;
  width: 15rem;
  min-height: 18rem;
  padding: 1rem;
  transition: box-shadow .2s;
  display: flex;
  box-shadow: 0 4px 6px #0000001a;
}

.card:hover {
  box-shadow: 0 6px 12px #00000026;
}

.card img {
  border-radius: 4px;
  width: 120px;
  height: 150px;
  margin: auto;
}

.card h3 {
  margin: .5rem 0;
  font-size: 1rem;
  font-weight: 600;
}

.card p {
  color: #555;
  flex-grow: 1;
  margin-bottom: .5rem;
  font-size: .875rem;
}

.card .price {
  margin: .5rem 0;
  font-size: 1rem;
  font-weight: bold;
}

.card button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: .5rem;
  font-weight: bold;
}

.card button:hover {
  background-color: #0056b3;
}

@media (width <= 899px) and (width >= 600px) {
  .card {
    max-width: 100%;
  }

  .card-container {
    justify-content: space-evenly;
  }
}

@media (width <= 599px) {
  .card {
    width: 100%;
    max-width: 10rem;
  }

  .cart {
    width: 40% !important;
  }

  .card-container {
    justify-content: center;
  }
}

.footer {
  justify-content: space-between;
  display: flex;
}

.empty-cart-info {
  background: #fff;
  width: 80%;
  height: 100px;
  padding: 0 .75rem;
}

.cross-mark {
  text-align: right;
  margin-bottom: -2rem;
  margin-right: -.5rem;
  font-size: 1.5rem;
  position: relative;
  top: -2rem;
  right: -1rem;
}

.cross-mark-wrapper:hover {
  color: #e23232;
  cursor: pointer;
}

.notification {
  z-index: 1000;
  color: #fff;
  background: #8b0000;
  border-radius: 5px;
  padding: 1rem 5rem;
  font-weight: bold;
  transition: transform .5s;
  position: fixed;
  top: 0;
  left: 35%;
  transform: translateY(-4rem);
  box-shadow: 2px 2px 8px #0003;
}

.show {
  transform: translateY(0);
}
